 100%;\n  height: 100%;\n  -o-object-fit: cover;\n     object-fit: cover;\n}\n.scanner-canvas[data-v-13c86778] {\n  display: none;\n}\n.scanner-frame[data-v-13c86778] {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 200px;\n  height: 200px;\n  border: 2px solid #3490dc;\n  box-shadow: 0 0 0 2000px rgba(0, 0, 0, 0.5);\n}\n.alert[data-v-13c86778] {\n  margin-top: 20px;\n  padding: 10px;\n  border-radius: 4px;\n  background-color: #f8d7da;\n  color: #721c24;\n  text-align: left;\n}\n.debug-section[data-v-13c86778] {\n  margin-top: 20px;\n  padding: 15px;\n  border: 1px dashed #ccc;\n  border-radius: 4px;\n}\n.debug-section h3[data-v-13c86778] {\n  margin-top: 0;\n  margin-bottom: 10px;\n  font-size: 16px;\n  color: #666;\n}\n.debug-input[data-v-13c86778] {\n  width: 100%;\n  padding: 8px;\n  margin-bottom: 10px;\n  border: 1px solid #ddd;\n  border-radius: 4px;\n}\n.debug-button[data-v-13c86778] {\n  background-color: #6c757d;\n  color: white;\n  border: none;\n  padding: 8px 16px;\n  border-radius: 4px;\n  cursor: pointer;\n}\n.debug-button[data-v-13c86778]:hover {\n  background-color: #5a6268;\n}\n", "",{"version":3,"sources":["webpack://./resources/js/components/Ddt/ScanQr.vue"],"names":[],"mappings":";AAoOA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,iBAAA;EACA,aAAA;EACA,yBAAA;AACA;AAEA;EACA,WAAA;EACA,gBAAA;EACA,aAAA;EACA,iBAAA;EACA,kBAAA;EACA,wCAAA;EACA,kBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,gBAAA;EACA,YAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,YAAA;EACA,aAAA;EACA,cAAA;EACA,0BAAA;EACA,kBAAA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;AACA;AAEA;EACA,WAAA;EACA,YAAA;EACA,kBAAA;EACA,yBAAA;EACA,YAAA;EACA,YAAA;EACA,eAAA;EACA,eAAA;EACA,iCAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,eAAA;EACA,MAAA;EACA,OAAA;EACA,QAAA;EACA,SAAA;EACA,oCAAA;EACA,aAAA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;AACA;AAEA;EACA,UAAA;EACA,gBAAA;EACA,uBAAA;EACA,kBAAA;EACA,gBAAA;AACA;AAEA;EACA,aAAA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,yBAAA;AACA;AAEA;EACA,SAAA;EACA,eAAA;AACA;AAEA;EACA,gBAAA;EACA,YAAA;EACA,eAAA;EACA,eAAA;AACA;AAEA;EACA,kBAAA;EACA,aAAA;AACA;AAEA;EACA,WAAA;EACA,YAAA;EACA,oBAAA;KAAA,iBAAA;AACA;AAEA;EACA,aAAA;AACA;AAEA;EACA,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;EACA,YAAA;EACA,aAAA;EACA,yBAAA;EACA,2CAAA;AACA;AAEA;EACA,gBAAA;EACA,aAAA;EACA,kBAAA;EACA,yBAAA;EACA,cAAA;EACA,gBAAA;AACA;AAEA;EACA,gBAAA;EACA,aAAA;EACA,uBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,eAAA;EACA,WAAA;AACA;AAEA;EACA,WAAA;EACA,YAAA;EACA,mBAAA;EACA,sBAAA;EACA,kBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;EACA,YAAA;EACA,iBAAA;EACA,kBAAA;EACA,eAAA;AACA;AAEA;EACA,yBAAA;AACA","sourcesContent":["// resources/js/components/Ddt/ScanQr.vue\n\n<template>\n  <div class=\"scan-container\">\n    <div class=\"scan-card\">\n      <div class=\"logo\">\n        <img src=\"/img/logo.png\" alt=\"B&C Prodotti Chimici\" />\n      </div>\n\n      <h2>SCANSIONA QR CODE</h2>\n\n      <div class=\"camera-container\">\n        <div class=\"qr-border\">\n          <button @click=\"startScan\" class=\"camera-button\">\n            <i class=\"fas fa-camera\"></i>\n          </button>\n        </div>\n      </div>\n\n      <div v-if=\"error\" class=\"alert alert-danger mt-3\">\n        {{ error }}\n      </div>\n\n      <!-- Componente per la scansione della fotocamera -->\n      <div v-if=\"scanning\" class=\"scanner-overlay\">\n        <div class=\"scanner-container\">\n          <div class=\"scanner-header\">\n            <h3>Inquadra il QR Code</h3>\n            <button @click=\"stopScan\" class=\"close-btn\">&times;</button>\n          </div>\n\n          <div class=\"scanner-viewport\">\n            <video ref=\"video\" class=\"scanner-video\"></video>\n            <canvas ref=\"canvas\" class=\"scanner-canvas\"></canvas>\n            <div class=\"scanner-frame\"></div>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { mapActions } from 'vuex';\nimport jsQR from 'jsqr';\n\nexport default {\n  data() {\n    return {\n      scanning: false,\n      error: null,\n      video: null,\n      canvas: null,\n      canvasCtx: null,\n      animationFrame: null,\n      stream: null,\n      loading: false,\n      debugQrCode: '',\n      isDevEnvironment: process.env.NODE_ENV === 'development'\n    };\n  },\n\n  methods: {\n    ...mapActions('ddt', ['fetchDdtByQrCode']),\n\n    startScan() {\n      console.log(\"Avvio scansione\");\n      this.scanning = true;\n      this.error = null;\n\n      this.$nextTick(() => {\n        this.initCamera();\n      });\n    },\n\n    stopScan() {\n      console.log(\"Stop scansione\");\n      this.scanning = false;\n\n      if (this.animationFrame) {\n        cancelAnimationFrame(this.animationFrame);\n        this.animationFrame = null;\n      }\n\n      if (this.stream) {\n        this.stream.getTracks().forEach(track => track.stop());\n        this.stream = null;\n      }\n    },\n\n    async initCamera() {\n      console.log(\"Inizializzazione fotocamera\");\n      try {\n        this.video = this.$refs.video;\n        this.canvas = this.$refs.canvas;\n        this.canvasCtx = this.canvas.getContext('2d');\n\n        // Accesso alla fotocamera\n        this.stream = await navigator.mediaDevices.getUserMedia({\n          video: {\n            facingMode: 'environment',\n            width: { ideal: 1280 },\n            height: { ideal: 720 }\n          }\n        });\n\n        this.video.srcObject = this.stream;\n        this.video.setAttribute('playsinline', true);\n\n        // Attendi che il video sia pronto\n        this.video.onloadedmetadata = () => {\n          console.log(\"Video pronto per la riproduzione\");\n          this.video.play();\n\n          // Imposta le dimensioni del canvas in base al video\n          this.canvas.width = this.video.videoWidth;\n          this.canvas.height = this.video.videoHeight;\n\n          console.log(\"Dimensioni canvas:\", this.canvas.width, \"x\", this.canvas.height);\n\n          // Avvia la scansione\n          this.scanQrCode();\n        };\n      } catch (err) {\n        console.error(\"Errore accesso fotocamera:\", err);\n        this.error = 'Impossibile accedere alla fotocamera. Verifica i permessi.';\n        this.scanning = false;\n      }\n    },\n\n    scanQrCode() {\n      if (!this.scanning) {\n        console.log(\"Scansione interrotta\");\n        return;\n      }\n\n      // Verifica se il video è attivo e pronto\n      if (this.video.readyState === this.video.HAVE_ENOUGH_DATA) {\n        // Disegna il frame del video sul canvas\n        this.canvasCtx.drawImage(\n          this.video,\n          0,\n          0,\n          this.canvas.width,\n          this.canvas.height\n        );\n\n        // Ottieni i dati dell'immagine dal canvas\n        const imageData = this.canvasCtx.getImageData(\n          0,\n          0,\n          this.canvas.width,\n          this.canvas.height\n        );\n\n        // Usa jsQR per decodificare il QR code\n        const code = jsQR(\n          imageData.data,\n          imageData.width,\n          imageData.height,\n          {\n            inversionAttempts: \"dontInvert\"\n          }\n        );\n\n        // Se viene trovato un QR code\n        if (code) {\n          console.log(\"QR Code rilevato:\", code.data);\n          this.handleQrCode(code.data);\n          return;\n        }\n      }\n\n      // Continua la scansione\n      this.animationFrame = requestAnimationFrame(() => this.scanQrCode());\n    },\n\n    async handleQrCode(qrData) {\n      console.log(\"Gestione QR code:\", qrData);\n      this.stopScan();\n\n      if (!qrData) {\n        this.error = 'QR Code non valido o vuoto';\n        return;\n      }\n\n      try {\n        this.loading = true;\n\n        console.log(\"Invio richiesta al server con qr_code:\", qrData);\n        const response = await this.fetchDdtByQrCode({ qr_code: qrData });\n        console.log(\"Risposta server:\", response);\n\n        if (response.success) {\n          console.log(\"Documento trovato, navigazione a document-detail\");\n          console.log(\"SaleDocId:\", response.document.SaleDocId);\n          console.log(\"Dettagli:\", response.details);\n\n          const firstDetailLine = response.details && response.details.length > 0\n            ? response.details[0].Line\n            : null;\n\n          console.log(\"Prima linea dettaglio:\", firstDetailLine);\n\n          // Naviga alla pagina dei dettagli del documento\n          this.$router.push({\n            name: 'document-detail',\n            params: {\n              saleDocId: response.document.SaleDocId,\n              lineId: firstDetailLine\n            }\n          });\n        } else {\n          console.error(\"Errore recupero documento:\", response.message);\n          this.error = response.message || 'Documento non trovato';\n        }\n      } catch (err) {\n        console.error(\"Eccezione durante la gestione del QR code:\", err);\n        this.error = 'Errore durante la lettura del QR Code';\n      } finally {\n        this.loading = false;\n      }\n    },\n  }\n};\n</script>\n\n<style scoped>\n.scan-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  min-height: 100vh;\n  padding: 20px;\n  background-color: #f5f5f5;\n}\n\n.scan-card {\n  width: 100%;\n  max-width: 400px;\n  padding: 30px;\n  background: white;\n  border-radius: 8px;\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\n  text-align: center;\n}\n\n.logo {\n  margin-bottom: 20px;\n}\n\n.logo img {\n  max-width: 180px;\n  height: auto;\n}\n\n.camera-container {\n  margin: 30px auto;\n}\n\n.qr-border {\n  width: 200px;\n  height: 200px;\n  margin: 0 auto;\n  border: 2px dashed #3490dc;\n  border-radius: 50%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.camera-button {\n  width: 80px;\n  height: 80px;\n  border-radius: 50%;\n  background-color: #3490dc;\n  color: white;\n  border: none;\n  font-size: 30px;\n  cursor: pointer;\n  transition: background-color 0.3s;\n}\n\n.camera-button:hover {\n  background-color: #2779bd;\n}\n\n.scanner-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: rgba(0, 0, 0, 0.8);\n  z-index: 1000;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.scanner-container {\n  width: 90%;\n  max-width: 500px;\n  background-color: white;\n  border-radius: 8px;\n  overflow: hidden;\n}\n\n.scanner-header {\n  padding: 15px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  background-color: #f8f9fa;\n}\n\n.scanner-header h3 {\n  margin: 0;\n  font-size: 18px;\n}\n\n.close-btn {\n  background: none;\n  border: none;\n  font-size: 24px;\n  cursor: pointer;\n}\n\n.scanner-viewport {\n  position: relative;\n  height: 300px;\n}\n\n.scanner-video {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n}\n\n.scanner-canvas {\n  display: none;\n}\n\n.scanner-frame {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 200px;\n  height: 200px;\n  border: 2px solid #3490dc;\n  box-shadow: 0 0 0 2000px rgba(0, 0, 0, 0.5);\n}\n\n.alert {\n  margin-top: 20px;\n  padding: 10px;\n  border-radius: 4px;\n  background-color: #f8d7da;\n  color: #721c24;\n  text-align: left;\n}\n\n.debug-section {\n  margin-top: 20px;\n  padding: 15px;\n  border: 1px dashed #ccc;\n  border-radius: 4px;\n}\n\n.debug-section h3 {\n  margin-top: 0;\n  margin-bottom: 10px;\n  font-size: 16px;\n  color: #666;\n}\n\n.debug-input {\n  width: 100%;\n  padding: 8px;\n  margin-bottom: 10px;\n  border: 1px solid #ddd;\n  border-radius: 4px;\n}\n\n.debug-button {\n  background-color: #6c757d;\n  color: white;\n  border: none;\n  padding: 8px 16px;\n  border-radius: 4px;\n  cursor: pointer;\n}\n\n.debug-button:hover {\n  background-color: #5a6268;\n}\n</style>\n"],"sourceRoot":""}]);
