A,gBAAA;EACA,YAAA;AACA;AAEA;EACA,kBAAA;EACA,mBAAA;EACA,WAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,cAAA;EACA,kBAAA;EACA,gBAAA;EACA,WAAA;AACA;AAEA;EACA,WAAA;EACA,aAAA;EACA,eAAA;EACA,yBAAA;EACA,kBAAA;EACA,0CAAA;AACA;AAEA;EACA,aAAA;EACA,qBAAA;EACA,6CAAA;AACA;AAEA;EACA,yBAAA;EACA,aAAA;AACA;;AAEA,4CAAA;AACA;EACA,kBAAA;AACA;AAEA;EACA,mBAAA,EAAA,uCAAA;AACA;AAEA;EACA,kBAAA;EACA,QAAA;EACA,MAAA;EACA,YAAA;EACA,WAAA;EACA,gBAAA;EACA,YAAA;EACA,eAAA;EACA,cAAA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,sBAAA;AACA;AAEA;EACA,cAAA;AACA;AAEA;EACA,mBAAA;EACA,aAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,gBAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,eAAA;EACA,eAAA;AACA;AAEA;EACA,cAAA;EACA,WAAA;EACA,aAAA;EACA,eAAA;EACA,gBAAA;EACA,kBAAA;EACA,YAAA;EACA,kBAAA;EACA,eAAA;EACA,oBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;EACA,mBAAA;AACA;;AAEA,4CAAA;AACA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,QAAA;AACA;AAEA;EACA,eAAA;AACA;;AAEA,uDAAA;AACA;AACA,KAAA,uBAAA;AAAA;AACA,OAAA,yBAAA;AAAA;AACA;AAEA;EACA,2CAAA;AACA;;AAEA,kBAAA;AACA;EACA,kBAAA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,cAAA;EACA,yBAAA;EACA,yBAAA;AACA;;AAEA,2BAAA;AACA;AACA;IACA,aAAA;AACA;AAEA;IACA,eAAA;IACA,aAAA;AACA;AAEA;IACA,mBAAA;AACA;AAEA;IACA,WAAA;AACA;AACA","sourcesContent":["<template>\n  <div class=\"login-container\">\n    <div class=\"login-card\">\n      <div class=\"logo\">\n        <img src=\"/img/logo.png\" alt=\"B&C Prodotti Chimici\" />\n      </div>\n\n      <h2>Accesso</h2>\n\n      <div v-if=\"error\" class=\"alert alert-danger\">\n        {{ error }}\n      </div>\n\n      <form @submit.prevent=\"login\">\n        <div class=\"form-group\">\n          <label for=\"email\">Email</label>\n          <input\n            type=\"email\"\n            id=\"email\"\n            v-model=\"form.email\"\n            class=\"form-control\"\n            required\n            :disabled=\"loading\"\n          />\n        </div>\n\n        <div class=\"form-group\">\n          <label for=\"password\">Password</label>\n          <div class=\"password-container\">\n            <input\n              :type=\"showPassword ? 'text' : 'password'\"\n              id=\"password\"\n              v-model=\"form.password\"\n              class=\"form-control password-input\"\n              required\n              :disabled=\"loading\"\n            />\n            <button\n              type=\"button\"\n              class=\"password-toggle\"\n              @click=\"togglePasswordVisibility\"\n              :disabled=\"loading\"\n            >\n              <i :class=\"showPassword ? 'fas fa-eye-slash' : 'fas fa-eye'\"></i>\n            </button>\n          </div>\n        </div>\n\n        <div class=\"form-group form-check\">\n          <input\n            type=\"checkbox\"\n            id=\"certify\"\n            v-model=\"form.certifyDevice\"\n            class=\"form-check-input\"\n            :disabled=\"loading\"\n          />\n          <label class=\"form-check-label\" for=\"certify\">\n            Certifica questo dispositivo come sicuro\n          </label>\n        </div>\n\n        <button type=\"submit\" class=\"btn btn-primary btn-block\" :disabled=\"loading\">\n          <span v-if=\"loading\" class=\"loading-content\">\n            <i class=\"fas fa-spinner fa-spin\"></i>\n            Accesso in corso...\n          </span>\n          <span v-else>\n            Accedi\n          </span>\n        </button>\n      </form>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      form: {\n        email: '',\n        password: '',\n        certifyDevice: false\n      },\n      error: null,\n      loading: false,\n      showPassword: false\n    };\n  },\n\n  created() {\n    // Verifica se il dispositivo è già certificato\n    this.checkDeviceCertification();\n  },\n\n  methods: {\n    togglePasswordVisibility() {\n      this.showPassword = !this.showPassword;\n    },\n\n    async checkDeviceCertification() {\n      const deviceId = localStorage.getItem('device_id');\n      const token = localStorage.getItem('device_token');\n\n      if (!deviceId || !token) {\n        return;\n      }\n\n      try {\n        const response = await axios.get('/check-device', {\n          headers: {\n            'Device-ID': deviceId,\n            'Device-Token': token\n          }\n        });\n\n        if (response.data.authenticated) {\n          // Imposta headers per future richieste\n          axios.defaults.headers.common['Device-ID'] = deviceId;\n          axios.defaults.headers.common['Device-Token'] = token;\n\n          // Aggiorna lo stato dell'applicazione\n          this.$store.commit('auth/SET_USER', response.data.user);\n          this.$store.commit('auth/SET_AUTHENTICATED', true);\n\n          // Reindirizza\n          this.$router.push('/scan');\n        }\n      } catch (error) {\n        console.error('Errore verifica dispositivo:', error);\n      }\n    },\n\n    async login() {\n      this.loading = true;\n      this.error = null;\n\n      try {\n        const deviceId = this.generateDeviceId();\n\n        const response = await axios.post('/login', {\n          email: this.form.email,\n          password: this.form.password,\n          device_id: deviceId,\n          certify_device: this.form.certifyDevice\n        });\n\n        if (response.data.token) {\n          // Salva i dati del dispositivo\n          localStorage.setItem('device_id', deviceId);\n          localStorage.setItem('device_token', response.data.token);\n\n          // Imposta headers per future richieste\n          axios.defaults.headers.common['Device-ID'] = deviceId;\n          axios.defaults.headers.common['Device-Token'] = response.data.token;\n\n          // Aggiorna lo stato dell'applicazione\n          this.$store.commit('auth/SET_USER', response.data.user);\n          this.$store.commit('auth/SET_AUTHENTICATED', true);\n\n          // Reindirizza\n          this.$router.push('/scan');\n        }\n      } catch (error) {\n        console.error('Errore login:', error);\n        this.error = 'Credenziali non valide';\n      } finally {\n        this.loading = false;\n      }\n    },\n\n    generateDeviceId() {\n      let deviceId = localStorage.getItem('device_id');\n\n      if (!deviceId) {\n        deviceId = 'device_' + Math.random().toString(36).substring(2, 15);\n      }\n\n      return deviceId;\n    }\n  }\n};\n</script>\n\n<style scoped>\n.login-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  min-height: 100vh;\n  padding: 20px;\n  background-color: #f5f5f5;\n}\n\n.login-card {\n  width: 100%;\n  max-width: 400px;\n  padding: 30px;\n  background: white;\n  border-radius: 8px;\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\n}\n\n.logo {\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n.logo img {\n  max-width: 180px;\n  height: auto;\n}\n\nh2 {\n  text-align: center;\n  margin-bottom: 20px;\n  color: #333;\n}\n\n.form-group {\n  margin-bottom: 20px;\n}\n\n.form-group label {\n  display: block;\n  margin-bottom: 5px;\n  font-weight: 500;\n  color: #333;\n}\n\n.form-control {\n  width: 100%;\n  padding: 12px;\n  font-size: 16px;\n  border: 1px solid #ced4da;\n  border-radius: 4px;\n  transition: border-color 0.15s ease-in-out;\n}\n\n.form-control:focus {\n  outline: none;\n  border-color: #80bdff;\n  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.25);\n}\n\n.form-control:disabled {\n  background-color: #f8f9fa;\n  opacity: 0.65;\n}\n\n/* Styling per il container della password */\n.password-container {\n  position: relative;\n}\n\n.password-input {\n  padding-right: 45px; /* Spazio per il pulsante dell'occhio */\n}\n\n.password-toggle {\n  position: absolute;\n  right: 0;\n  top: 0;\n  height: 100%;\n  width: 45px;\n  background: none;\n  border: none;\n  cursor: pointer;\n  color: #6c757d;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: color 0.3s;\n}\n\n.password-toggle:hover:not(:disabled) {\n  color: #3490dc;\n}\n\n.password-toggle:disabled {\n  cursor: not-allowed;\n  opacity: 0.65;\n}\n\n.form-check {\n  display: flex;\n  align-items: center;\n  margin-top: 20px;\n}\n\n.form-check-input {\n  margin-right: 8px;\n}\n\n.form-check-label {\n  cursor: pointer;\n  font-size: 14px;\n}\n\n.btn {\n  display: block;\n  width: 100%;\n  padding: 12px;\n  font-size: 16px;\n  font-weight: 600;\n  text-align: center;\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n  transition: all 0.3s;\n}\n\n.btn-primary {\n  background-color: #3490dc;\n  color: white;\n}\n\n.btn-primary:hover:not(:disabled) {\n  background-color: #2779bd;\n}\n\n.btn-primary:disabled {\n  background-color: #a0c5e8;\n  cursor: not-allowed;\n}\n\n/* Styling per il contenuto di caricamento */\n.loading-content {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 8px;\n}\n\n.loading-content i {\n  font-size: 14px;\n}\n\n/* Animazione di rotazione per l'icona di caricamento */\n@keyframes spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n.fa-spin {\n  animation: spin 1s linear infinite;\n}\n\n/* Alert styling */\n.alert {\n  padding: 12px 16px;\n  margin-bottom: 15px;\n  border-radius: 4px;\n}\n\n.alert-danger {\n  color: #721c24;\n  background-color: #f8d7da;\n  border: 1px solid #f5c6cb;\n}\n\n/* Responsive adjustments */\n@media (max-width: 576px) {\n  .login-card {\n    padding: 20px;\n  }\n\n  .form-control {\n    font-size: 14px;\n    padding: 10px;\n  }\n\n  .password-input {\n    padding-right: 40px;\n  }\n\n  .password-toggle {\n    width: 40px;\n  }\n}\n</style>\n"],"sourceRoot":""}]);
