2]:first-child {\n  margin-right: 10px;\n}\n.info-label[data-v-7ff15712] {\n  font-size: 12px;\n  font-weight: bold;\n  color: #666;\n  margin-bottom: 5px;\n}\n.info-value[data-v-7ff15712] {\n  font-size: 16px;\n  font-weight: bold;\n}\n.item-info[data-v-7ff15712] {\n  background-color: #f5f5f5;\n  padding: 10px 15px;\n  margin-bottom: 10px;\n  border-radius: 4px;\n}\n.description[data-v-7ff15712] {\n  background-color: #f5f5f5;\n  padding: 10px 15px;\n  margin-bottom: 20px;\n  border-radius: 4px;\n  font-size: 14px;\n  min-height: 60px;\n}\n.quantity-section[data-v-7ff15712] {\n  margin-bottom: 30px;\n}\n.quantity-row[data-v-7ff15712] {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 10px;\n}\n.quantity-label[data-v-7ff15712] {\n  font-size: 14px;\n  font-weight: bold;\n}\n.quantity-input[data-v-7ff15712] {\n  border: 1px solid #ccc;\n  border-radius: 4px;\n  overflow: hidden;\n}\n.quantity-input input[data-v-7ff15712] {\n  width: 100%;\n  padding: 15px;\n  font-size: 22px;\n  text-align: center;\n  border: none;\n  outline: none;\n}\n.action-button[data-v-7ff15712] {\n  text-align: center;\n}\n.btn-next[data-v-7ff15712] {\n  background-color: #3490dc;\n  color: white;\n  border: none;\n  padding: 12px 40px;\n  border-radius: 25px;\n  font-size: 16px;\n  font-weight: bold;\n  cursor: pointer;\n  transition: background-color 0.3s;\n}\n.btn-next[data-v-7ff15712]:hover:not(:disabled) {\n  background-color: #2779bd;\n}\n.btn-next[data-v-7ff15712]:disabled {\n  background-color: #a0c5e8;\n  cursor: not-allowed;\n}\n@media (max-width: 576px) {\n.document-detail[data-v-7ff15712] {\n    padding: 15px;\n}\n.quantity-input input[data-v-7ff15712] {\n    font-size: 18px;\n    padding: 12px;\n}\n}\n", "",{"version":3,"sources":["webpack://./resources/js/components/Ddt/DocumentDetail.vue"],"names":[],"mappings":";AAuRA;EACA,aAAA;EACA,gBAAA;EACA,cAAA;EACA,kBAAA;EACA,gBAAA;AACA;AAEA;EACA,kBAAA;EACA,MAAA;EACA,OAAA;EACA,QAAA;EACA,SAAA;EACA,0CAAA;EACA,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;EACA,WAAA;AACA;AAEA;EACA,yBAAA;EACA,6BAAA;EACA,kBAAA;EACA,WAAA;EACA,YAAA;EACA,2CAAA;EACA,mBAAA;AACA;AAEA;AACA,KAAA,uBAAA;AAAA;AACA,OAAA,yBAAA;AAAA;AACA;AAEA;EACA,eAAA;EACA,WAAA;AACA;AAEA;EACA,yBAAA;EACA,cAAA;EACA,aAAA;EACA,kBAAA;EACA,mBAAA;EACA,kBAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,SAAA;AACA;AAEA;EACA,eAAA;EACA,kBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;EACA,YAAA;EACA,iBAAA;EACA,kBAAA;EACA,eAAA;EACA,eAAA;EACA,gBAAA;AACA;AAEA;EACA,kBAAA;EACA,mBAAA;AACA;AAEA;EACA,gBAAA;EACA,YAAA;AACA;AAEA;EACA,yBAAA;EACA,kBAAA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;AACA;AAEA;EACA,OAAA;EACA,kBAAA;EACA,yBAAA;EACA,kBAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,eAAA;EACA,iBAAA;EACA,WAAA;EACA,kBAAA;AACA;AAEA;EACA,eAAA;EACA,iBAAA;AACA;AAEA;EACA,yBAAA;EACA,kBAAA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,yBAAA;EACA,kBAAA;EACA,mBAAA;EACA,kBAAA;EACA,eAAA;EACA,gBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;AACA;AAEA;EACA,eAAA;EACA,iBAAA;AACA;AAEA;EACA,sBAAA;EACA,kBAAA;EACA,gBAAA;AACA;AAEA;EACA,WAAA;EACA,aAAA;EACA,eAAA;EACA,kBAAA;EACA,YAAA;EACA,aAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;EACA,YAAA;EACA,kBAAA;EACA,mBAAA;EACA,eAAA;EACA,iBAAA;EACA,eAAA;EACA,iCAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;EACA,mBAAA;AACA;AAEA;AACA;IACA,aAAA;AACA;AAEA;IACA,eAAA;IACA,aAAA;AACA;AACA","sourcesContent":["// resources/js/components/Ddt/DocumentDetail.vue\n\n<template>\n  <div class=\"document-detail\">\n    <!-- Mostra loader durante il caricamento -->\n    <div v-if=\"loading\" class=\"loading-overlay\">\n      <div class=\"loading-spinner\"></div>\n      <div class=\"loading-text\">Caricamento in corso...</div>\n    </div>\n\n    <!-- Mostra errore se presente -->\n    <div v-else-if=\"error\" class=\"error-message\">\n      <div class=\"error-icon\">⚠️</div>\n      <div>{{ error }}</div>\n      <button @click=\"loadDocumentDetail\" class=\"retry-button\">Riprova</button>\n    </div>\n\n    <!-- Contenuto principale (mostra solo se detail e document esistono) -->\n    <template v-else-if=\"detail && document\">\n      <div class=\"logo-header\">\n        <img src=\"/img/logo.png\" alt=\"B&C Prodotti Chimici\" />\n      </div>\n\n      <div class=\"client-info\">\n        <div class=\"info-label\">CLIENTE:</div>\n        <div class=\"info-value\">{{ customerName }}</div>\n      </div>\n\n      <div class=\"document-info\">\n        <div class=\"info-row\">\n          <div class=\"info-col\">\n            <div class=\"info-label\">DDT NR.</div>\n            <div class=\"info-value\">{{ docNumber }}</div>\n          </div>\n          <div class=\"info-col\">\n            <div class=\"info-label\">DATA</div>\n            <div class=\"info-value\">{{ formattedDate }}</div>\n          </div>\n        </div>\n      </div>\n\n      <div class=\"item-info\">\n        <div class=\"info-label\">ART.</div>\n        <div class=\"info-value\">{{ detail.Item }}</div>\n      </div>\n\n      <div class=\"description\">\n        {{ detail.Description }}\n      </div>\n\n      <div class=\"quantity-section\">\n        <div class=\"quantity-row\">\n          <div class=\"quantity-label\">QUANTITA'</div>\n          <div class=\"quantity-label\">{{ detail.UoM || 'LITRI' }}</div>\n        </div>\n\n        <div class=\"quantity-input\">\n          <input\n            type=\"number\"\n            v-model.number=\"quantity\"\n            class=\"form-control\"\n            step=\"0.1\"\n          />\n        </div>\n      </div>\n\n      <div class=\"action-button\">\n        <button @click=\"saveAndContinue\" class=\"btn-next\" :disabled=\"loading\">\n          {{ loading ? 'Salvataggio...' : 'AVANTI' }}\n        </button>\n      </div>\n    </template>\n\n    <!-- Fallback se non ci sono dati -->\n    <div v-else class=\"error-message\">\n      <div>Nessun dato disponibile. Assicurati di aver scansionato un codice QR valido.</div>\n      <button @click=\"goToScan\" class=\"retry-button\">Scansiona codice</button>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { mapState, mapGetters, mapActions } from 'vuex';\n\nexport default {\n  data() {\n    return {\n      quantity: 0,\n      saleDocId: null,\n      lineId: null,\n      localLoading: false\n    };\n  },\n\n  computed: {\n    ...mapState('ddt', ['document', 'detail', 'customer']),\n    ...mapGetters('ddt', ['isLoading', 'hasError', 'errorMessage']),\n\n    loading() {\n      return this.isLoading || this.localLoading;\n    },\n\n    error() {\n      return this.errorMessage;\n    },\n\n    customerName() {\n      return this.customer?.CompanyName || '';\n    },\n\n    docNumber() {\n      return this.document?.DocNo || '';\n    },\n\n    formattedDate() {\n      if (!this.document?.DocumentDate) return '';\n\n      // Formato: GG/MM/AAAA\n      try {\n        const date = new Date(this.document.DocumentDate);\n        const day = date.getDate().toString().padStart(2, '0');\n        const month = (date.getMonth() + 1).toString().padStart(2, '0');\n        const year = date.getFullYear();\n\n        return `${day}/${month}/${year}`;\n      } catch (e) {\n        console.error(\"Errore nella formattazione della data:\", e);\n        return this.document.DocumentDate || '';\n      }\n    }\n  },\n\n  created() {\n    console.log(\"DocumentDetail creato\");\n    this.initializeComponent();\n  },\n\n  watch: {\n    // Osserva i cambiamenti nei parametri della rotta\n    '$route.params': {\n      handler(newParams) {\n        console.log(\"Parametri rotta cambiati:\", newParams);\n        this.initializeComponent();\n      },\n      deep: true,\n      immediate: true\n    },\n\n    // Quando detail cambia, aggiorna la quantità\n    detail(newDetail) {\n      console.log(\"Detail cambiato:\", newDetail);\n      if (newDetail) {\n        this.quantity = parseFloat(newDetail.Qty) || 0;\n      }\n    }\n  },\n\n  methods: {\n    ...mapActions('ddt', ['fetchDocumentDetail', 'updateQuantity', 'getNextDetail']),\n\n    initializeComponent() {\n      this.saleDocId = this.$route.params.saleDocId;\n      this.lineId = this.$route.params.lineId;\n\n      console.log(\"Inizializzazione con params:\", {\n        saleDocId: this.saleDocId,\n        lineId: this.lineId\n      });\n\n      if (this.saleDocId) {\n        this.loadDocumentDetail();\n      } else {\n        console.error(\"SaleDocId mancante\");\n      }\n    },\n\n    async loadDocumentDetail() {\n      console.log(\"Caricamento documento:\", this.saleDocId, \"linea:\", this.lineId);\n\n      if (!this.saleDocId) {\n        console.error(\"SaleDocId non impostato\");\n        return;\n      }\n\n      this.localLoading = true;\n\n      try {\n        const response = await this.fetchDocumentDetail({\n          saleDocId: this.saleDocId,\n          line: this.lineId\n        });\n\n        console.log(\"Risposta caricamento:\", response);\n\n        if (response.success) {\n          console.log(\"Caricamento riuscito - Detail:\", this.detail);\n          console.log(\"Caricamento riuscito - Document:\", this.document);\n\n          // Imposta la quantità iniziale dal dettaglio caricato\n          if (this.detail) {\n            this.quantity = parseFloat(this.detail.Qty) || 0;\n          } else {\n            console.error(\"Detail non impostato dopo il caricamento\");\n          }\n        } else {\n          console.error(\"Errore caricamento:\", response);\n        }\n      } catch (err) {\n        console.error(\"Eccezione nel caricamento dei dettagli:\", err);\n      } finally {\n        this.localLoading = false;\n      }\n    },\n\n    async saveAndContinue() {\n      console.log(\"Salva e continua - Detail:\", this.detail);\n\n      if (!this.detail) {\n        console.error(\"Detail non impostato, impossibile salvare\");\n        return;\n      }\n\n      this.localLoading = true;\n\n      try {\n        // Salva la quantità corrente\n        const updateResult = await this.updateQuantity({\n          sale_doc_id: this.saleDocId,\n          line: this.detail.Line,\n          item: this.detail.Item,\n          quantity: this.quantity\n        });\n\n        console.log(\"Risultato aggiornamento:\", updateResult);\n\n        if (!updateResult.success) {\n          console.error(\"Errore nell'aggiornamento della quantità\");\n          return;\n        }\n\n        // Ottieni il prossimo dettaglio, se esiste\n        const nextDetail = await this.getNextDetail({\n          saleDocId: this.saleDocId,\n          currentLine: this.detail.Line\n        });\n\n        console.log(\"Prossimo dettaglio:\", nextDetail);\n\n        if (nextDetail) {\n          // Vai al prossimo dettaglio\n          this.$router.push({\n            name: 'document-detail',\n            params: {\n              saleDocId: this.saleDocId,\n              lineId: nextDetail.Line\n            }\n          });\n        } else {\n          // Vai alla pagina di riepilogo\n                this.$router.push({\n                name: 'items-list',  // Assicurati che questa rotta esista nel tuo router\n                params: { saleDocId: this.saleDocId }\n            });\n        }\n      } catch (err) {\n        console.error(\"Errore nel salvataggio:\", err);\n      } finally {\n        this.localLoading = false;\n      }\n    },\n\n    goToScan() {\n      this.$router.push({ name: 'scan-qr' });\n    }\n  }\n};\n</script>\n\n<style scoped>\n.document-detail {\n  padding: 20px;\n  max-width: 500px;\n  margin: 0 auto;\n  position: relative;\n  min-height: 80vh;\n}\n\n.loading-overlay {\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: rgba(255, 255, 255, 0.9);\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  z-index: 10;\n}\n\n.loading-spinner {\n  border: 4px solid #f3f3f3;\n  border-top: 4px solid #3490dc;\n  border-radius: 50%;\n  width: 40px;\n  height: 40px;\n  animation: spin 1s linear infinite;\n  margin-bottom: 10px;\n}\n\n@keyframes spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n.loading-text {\n  font-size: 16px;\n  color: #333;\n}\n\n.error-message {\n  background-color: #f8d7da;\n  color: #721c24;\n  padding: 15px;\n  border-radius: 4px;\n  margin-bottom: 20px;\n  text-align: center;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 10px;\n}\n\n.error-icon {\n  font-size: 24px;\n  margin-bottom: 5px;\n}\n\n.retry-button {\n  background-color: #3490dc;\n  color: white;\n  border: none;\n  padding: 8px 16px;\n  border-radius: 4px;\n  cursor: pointer;\n  font-size: 14px;\n  margin-top: 10px;\n}\n\n.logo-header {\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n.logo-header img {\n  max-width: 120px;\n  height: auto;\n}\n\n.client-info {\n  background-color: #f5f5f5;\n  padding: 10px 15px;\n  margin-bottom: 10px;\n  border-radius: 4px;\n}\n\n.document-info {\n  margin-bottom: 20px;\n}\n\n.info-row {\n  display: flex;\n  justify-content: space-between;\n}\n\n.info-col {\n  flex: 1;\n  padding: 10px 15px;\n  background-color: #f5f5f5;\n  border-radius: 4px;\n}\n\n.info-col:first-child {\n  margin-right: 10px;\n}\n\n.info-label {\n  font-size: 12px;\n  font-weight: bold;\n  color: #666;\n  margin-bottom: 5px;\n}\n\n.info-value {\n  font-size: 16px;\n  font-weight: bold;\n}\n\n.item-info {\n  background-color: #f5f5f5;\n  padding: 10px 15px;\n  margin-bottom: 10px;\n  border-radius: 4px;\n}\n\n.description {\n  background-color: #f5f5f5;\n  padding: 10px 15px;\n  margin-bottom: 20px;\n  border-radius: 4px;\n  font-size: 14px;\n  min-height: 60px;\n}\n\n.quantity-section {\n  margin-bottom: 30px;\n}\n\n.quantity-row {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 10px;\n}\n\n.quantity-label {\n  font-size: 14px;\n  font-weight: bold;\n}\n\n.quantity-input {\n  border: 1px solid #ccc;\n  border-radius: 4px;\n  overflow: hidden;\n}\n\n.quantity-input input {\n  width: 100%;\n  padding: 15px;\n  font-size: 22px;\n  text-align: center;\n  border: none;\n  outline: none;\n}\n\n.action-button {\n  text-align: center;\n}\n\n.btn-next {\n  background-color: #3490dc;\n  color: white;\n  border: none;\n  padding: 12px 40px;\n  border-radius: 25px;\n  font-size: 16px;\n  font-weight: bold;\n  cursor: pointer;\n  transition: background-color 0.3s;\n}\n\n.btn-next:hover:not(:disabled) {\n  background-color: #2779bd;\n}\n\n.btn-next:disabled {\n  background-color: #a0c5e8;\n  cursor: not-allowed;\n}\n\n@media (max-width: 576px) {\n  .document-detail {\n    padding: 15px;\n  }\n\n  .quantity-input input {\n    font-size: 18px;\n    padding: 12px;\n  }\n}\n</style>\n"],"sourceRoot":""}]);
