rst-child {\n  margin-right: 10px;\n}\n.info-label[data-v-437ed017] {\n  font-size: 12px;\n  font-weight: bold;\n  color: #666;\n  margin-bottom: 5px;\n}\n.info-value[data-v-437ed017] {\n  font-size: 16px;\n  font-weight: bold;\n}\n.section-title[data-v-437ed017] {\n  font-size: 16px;\n  font-weight: bold;\n  margin-bottom: 15px;\n}\n.imballi-section[data-v-437ed017] {\n  margin-bottom: 20px;\n}\n.select-container[data-v-437ed017] {\n  margin-bottom: 15px;\n}\n.form-control[data-v-437ed017] {\n  width: 100%;\n  padding: 10px;\n  border: 1px solid #ced4da;\n  border-radius: 4px;\n  font-size: 16px;\n  -webkit-appearance: none;\n     -moz-appearance: none;\n          appearance: none;\n  background-image: url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 24 24' fill='none' stroke='%23333' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3E%3Cpolyline points='6 9 12 15 18 9'/%3E%3C/svg%3E\");\n  background-repeat: no-repeat;\n  background-position: right 10px center;\n  background-size: 16px;\n}\n.selected-package[data-v-437ed017] {\n  margin-bottom: 15px;\n}\n.package-item[data-v-437ed017] {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 10px 15px;\n  background-color: #f5f5f5;\n  border-radius: 4px;\n}\n.package-check[data-v-437ed017] {\n  color: #28a745;\n  font-size: 18px;\n}\n.quantity-section[data-v-437ed017] {\n  margin-bottom: 20px;\n}\n.quantity-row[data-v-437ed017] {\n  margin-bottom: 10px;\n}\n.quantity-label[data-v-437ed017] {\n  font-size: 14px;\n  font-weight: bold;\n}\n.quantity-input-group[data-v-437ed017] {\n  display: flex;\n  border: 1px solid #ced4da;\n  border-radius: 4px;\n  overflow: hidden;\n}\n.qty-btn[data-v-437ed017] {\n  width: 50px;\n  background-color: #f0f0f0;\n  border: none;\n  font-size: 20px;\n  cursor: pointer;\n}\n.qty-input[data-v-437ed017] {\n  flex: 1;\n  text-align: center;\n  padding: 10px;\n  font-size: 20px;\n  border: none;\n  outline: none;\n  background: white;\n}\n.qty-input[data-v-437ed017]:focus {\n  background-color: #f8f9fa;\n}\n\n/* Nascondere i controlli di incremento/decremento del browser per input number */\n.qty-input[data-v-437ed017]::-webkit-outer-spin-button,\n.qty-input[data-v-437ed017]::-webkit-inner-spin-button {\n  -webkit-appearance: none;\n  margin: 0;\n}\n.packages-list[data-v-437ed017] {\n  margin-bottom: 20px;\n}\n.package-list-items[data-v-437ed017] {\n  background-color: #f5f5f5;\n  border-radius: 4px;\n  overflow: hidden;\n}\n.package-list-item[data-v-437ed017] {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 10px 15px;\n  border-bottom: 1px solid #e0e0e0;\n}\n.package-list-item[data-v-437ed017]:last-child {\n  border-bottom: none;\n}\n.package-details[data-v-437ed017] {\n  display: flex;\n  flex-direction: column;\n}\n.package-type[data-v-437ed017] {\n  font-weight: bold;\n}\n.package-qty[data-v-437ed017] {\n  font-size: 14px;\n  color: #666;\n}\n.remove-btn[data-v-437ed017] {\n  background: none;\n  border: none;\n  color: #dc3545;\n  font-size: 24px;\n  cursor: pointer;\n  padding: 0 5px;\n}\n.action-buttons[data-v-437ed017] {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n.btn-add[data-v-437ed017] {\n  background-color: #28a745;\n  color: white;\n  border: none;\n  padding: 10px;\n  border-radius: 4px;\n  font-size: 16px;\n  font-weight: bold;\n  cursor: pointer;\n}\n.btn-add[data-v-437ed017]:disabled {\n  background-color: #6c757d;\n  cursor: not-allowed;\n}\n.btn-next[data-v-437ed017] {\n  background-color: #3490dc;\n  color: white;\n  border: none;\n  padding: 12px 40px;\n  border-radius: 25px;\n  font-size: 16px;\n  font-weight: bold;\n  cursor: pointer;\n  margin: 0 auto;\n  display: block;\n  transition: background-color 0.3s;\n}\n.btn-next[data-v-437ed017]:hover:not(:disabled) {\n  background-color: #2779bd;\n}\n.btn-next[data-v-437ed017]:disabled {\n  background-color: #a0c5e8;\n  cursor: not-allowed;\n}\n@media (max-width: 576px) {\n.items-list[data-v-437ed017] {\n    padding: 15px;\n}\n}\n", "",{"version":3,"sources":["webpack://./resources/js/components/Ddt/ItemList.vue"],"names":[],"mappings":";AA8SA;EACA,aAAA;EACA,gBAAA;EACA,cAAA;EACA,kBAAA;EACA,gBAAA;AACA;AAEA;EACA,kBAAA;EACA,MAAA;EACA,OAAA;EACA,QAAA;EACA,SAAA;EACA,0CAAA;EACA,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;EACA,WAAA;AACA;AAEA;EACA,yBAAA;EACA,6BAAA;EACA,kBAAA;EACA,WAAA;EACA,YAAA;EACA,2CAAA;EACA,mBAAA;AACA;AAEA;AACA,KAAA,uBAAA;AAAA;AACA,OAAA,yBAAA;AAAA;AACA;AAEA;EACA,eAAA;EACA,WAAA;AACA;AAEA;EACA,yBAAA;EACA,cAAA;EACA,aAAA;EACA,kBAAA;EACA,mBAAA;EACA,kBAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,SAAA;AACA;AAEA;EACA,eAAA;EACA,kBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;EACA,YAAA;EACA,iBAAA;EACA,kBAAA;EACA,eAAA;EACA,eAAA;EACA,gBAAA;AACA;AAEA;EACA,kBAAA;EACA,mBAAA;AACA;AAEA;EACA,gBAAA;EACA,YAAA;AACA;AAEA;EACA,yBAAA;EACA,kBAAA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;AACA;AAEA;EACA,OAAA;EACA,kBAAA;EACA,yBAAA;EACA,kBAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,eAAA;EACA,iBAAA;EACA,WAAA;EACA,kBAAA;AACA;AAEA;EACA,eAAA;EACA,iBAAA;AACA;AAEA;EACA,eAAA;EACA,iBAAA;EACA,mBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,WAAA;EACA,aAAA;EACA,yBAAA;EACA,kBAAA;EACA,eAAA;EACA,wBAAA;KAAA,qBAAA;UAAA,gBAAA;EACA,8RAAA;EACA,4BAAA;EACA,sCAAA;EACA,qBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,kBAAA;EACA,yBAAA;EACA,kBAAA;AACA;AAEA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,eAAA;EACA,iBAAA;AACA;AAEA;EACA,aAAA;EACA,yBAAA;EACA,kBAAA;EACA,gBAAA;AACA;AAEA;EACA,WAAA;EACA,yBAAA;EACA,YAAA;EACA,eAAA;EACA,eAAA;AACA;AAEA;EACA,OAAA;EACA,kBAAA;EACA,aAAA;EACA,eAAA;EACA,YAAA;EACA,aAAA;EACA,iBAAA;AACA;AAEA;EACA,yBAAA;AACA;;AAEA,iFAAA;AACA;;EAEA,wBAAA;EACA,SAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,yBAAA;EACA,kBAAA;EACA,gBAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,kBAAA;EACA,gCAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,sBAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,eAAA;EACA,WAAA;AACA;AAEA;EACA,gBAAA;EACA,YAAA;EACA,cAAA;EACA,eAAA;EACA,eAAA;EACA,cAAA;AACA;AAEA;EACA,aAAA;EACA,sBAAA;EACA,SAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;EACA,YAAA;EACA,aAAA;EACA,kBAAA;EACA,eAAA;EACA,iBAAA;EACA,eAAA;AACA;AAEA;EACA,yBAAA;EACA,mBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;EACA,YAAA;EACA,kBAAA;EACA,mBAAA;EACA,eAAA;EACA,iBAAA;EACA,eAAA;EACA,cAAA;EACA,cAAA;EACA,iCAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;EACA,mBAAA;AACA;AAEA;AACA;IACA,aAAA;AACA;AACA","sourcesContent":["<template>\n  <div class=\"items-list\">\n    <!-- Mostra loader durante il caricamento -->\n    <div v-if=\"loading\" class=\"loading-overlay\">\n      <div class=\"loading-spinner\"></div>\n      <div class=\"loading-text\">Caricamento in corso...</div>\n    </div>\n\n    <!-- Mostra errore se presente -->\n    <div v-else-if=\"error\" class=\"error-message\">\n      <div class=\"error-icon\">⚠️</div>\n      <div>{{ error }}</div>\n      <button @click=\"loadDocumentData\" class=\"retry-button\">Riprova</button>\n    </div>\n\n    <!-- Contenuto principale -->\n    <template v-else-if=\"document\">\n      <div class=\"logo-header\">\n        <img src=\"/img/logo.png\" alt=\"B&C Prodotti Chimici\" />\n      </div>\n\n      <div class=\"client-info\">\n        <div class=\"info-label\">CLIENTE:</div>\n        <div class=\"info-value\">{{ customerName }}</div>\n      </div>\n\n      <div class=\"document-info\">\n        <div class=\"info-row\">\n          <div class=\"info-col\">\n            <div class=\"info-label\">DDT NR.</div>\n            <div class=\"info-value\">{{ docNumber }}</div>\n          </div>\n          <div class=\"info-col\">\n            <div class=\"info-label\">DATA</div>\n            <div class=\"info-value\">{{ formattedDate }}</div>\n          </div>\n        </div>\n      </div>\n\n      <div class=\"imballi-section\">\n      <div class=\"section-title\">IMBALLI</div>\n\n      <!-- Select dinamica per il tipo di imballo -->\n      <div class=\"select-container\">\n        <select v-model=\"selectedPackageType\" class=\"form-control\" :disabled=\"loadingPackages\">\n          <option v-for=\"pkg in packages\" :key=\"pkg.Item\" :value=\"pkg.Item\">\n            {{ pkg.Description }}\n          </option>\n        </select>\n      </div>\n\n        <!-- Sezione quantità -->\n        <div class=\"quantity-section\">\n          <div class=\"quantity-row\">\n            <div class=\"quantity-label\">QUANTITA' RESA</div>\n          </div>\n\n          <div class=\"quantity-input-group\">\n            <button @click=\"decreaseQty\" class=\"qty-btn\">-</button>\n            <input\n              v-model.number=\"packageQuantity\"\n              type=\"number\"\n              min=\"0\"\n              class=\"qty-input\"\n              @focus=\"$event.target.select()\"\n            />\n            <button @click=\"increaseQty\" class=\"qty-btn\">+</button>\n          </div>\n        </div>\n      </div>\n\n      <!-- Lista degli imballi aggiunti -->\n    <div class=\"packages-list\" v-if=\"packages.length > 0\">\n        <div class=\"section-title\">IMBALLI AGGIUNTI</div>\n        <div class=\"package-list-items\">\n            <div v-for=\"(pkg, index) in addedPackages\" :key=\"index\" class=\"package-list-item\">\n            <div class=\"package-details\">\n                <div class=\"package-type\">{{ getPackageName(pkg.type) }}</div>\n                <div class=\"package-qty\">{{ pkg.quantity }} pz</div>\n            </div>\n            <button @click=\"removePackage(index)\" class=\"remove-btn\">&times;</button>\n            </div>\n        </div>\n    </div>\n\n      <!-- Pulsanti azione -->\n      <div class=\"action-buttons\">\n        <button @click=\"addPackage\" class=\"btn-add\" :disabled=\"!canAddPackage\">\n          AGGIUNGI\n        </button>\n        <button @click=\"saveAndContinue\" class=\"btn-next\" :disabled=\"localLoading\">\n            {{ localLoading ? 'SALVATAGGIO...' : 'AVANTI' }}\n        </button>\n      </div>\n    </template>\n\n    <!-- Fallback se non ci sono dati -->\n    <div v-else class=\"error-message\">\n      <div>Nessun dato disponibile. Assicurati di aver scansionato un codice QR valido.</div>\n      <button @click=\"goToScan\" class=\"retry-button\">Scansiona codice</button>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { mapState, mapGetters, mapActions } from 'vuex';\n\nexport default {\n  data() {\n    return {\n      selectedPackageType: 'NESSUNO',\n      packageQuantity: 0,\n      packages: [],\n      addedPackages: [],\n      loadingPackages: false,\n      saleDocId: null,\n      localLoading: false,\n      error: null,\n      packageSelected: true\n    };\n  },\n\n  computed: {\n    ...mapState('ddt', ['document', 'customer']),\n    ...mapGetters('ddt', ['isLoading']),\n\n    loading() {\n      return this.isLoading || this.localLoading;\n    },\n\n    customerName() {\n      return this.customer?.CompanyName || '';\n    },\n\n    docNumber() {\n      return this.document?.DocNo || '';\n    },\n\n    formattedDate() {\n      if (!this.document?.DocumentDate) return '';\n\n      try {\n        const date = new Date(this.document.DocumentDate);\n        const day = date.getDate().toString().padStart(2, '0');\n        const month = (date.getMonth() + 1).toString().padStart(2, '0');\n        const year = date.getFullYear();\n\n        return `${day}/${month}/${year}`;\n      } catch (e) {\n        console.error(\"Errore nella formattazione della data:\", e);\n        return this.document.DocumentDate || '';\n      }\n    },\n\n    canAddPackage() {\n      return this.selectedPackageType;\n    },\n  },\n\n  created() {\n    console.log(\"ItemsList creato\");\n    this.initializeComponent();\n  },\n\n  methods: {\n    ...mapActions('ddt', ['fetchDdtById']),\n\n    initializeComponent() {\n      this.saleDocId = this.$route.params.saleDocId;\n\n      console.log(\"Inizializzazione con saleDocId:\", this.saleDocId);\n\n      if (this.saleDocId) {\n        this.loadDocumentData();\n      } else {\n        console.error(\"SaleDocId mancante\");\n      }\n    },\n\n    async loadDocumentData() {\n      this.localLoading = true;\n      this.error = null;\n\n      try {\n        await this.fetchDdtById({ saleDocId: this.saleDocId });\n      } catch (err) {\n        console.error(\"Errore nel caricamento del documento:\", err);\n        this.error = \"Errore nel caricamento del documento\";\n      } finally {\n        this.localLoading = false;\n        this.loadPackages();\n      }\n    },\n\n    async loadPackages() {\n      this.loadingPackages = true;\n\n      try {\n        const response = await axios.get('/packages');\n\n        if (response.data.success) {\n          console.log(\"Imballi caricati:\", response.data.packages);\n          this.packages = response.data.packages;\n        } else {\n          console.error(\"Errore nel caricamento degli imballi:\", response.data.message);\n          this.error = \"Errore nel caricamento degli imballi\";\n        }\n      } catch (err) {\n        console.error(\"Eccezione nel caricamento degli imballi:\", err);\n        this.error = \"Errore nella comunicazione con il server\";\n      } finally {\n        this.loadingPackages = false;\n      }\n    },\n\n    getPackageName(packageId) {\n      // Cerca l'imballo nell'array e restituisce la descrizione\n      const pkg = this.packages.find(p => p.Item === packageId);\n      return pkg ? pkg.Description : packageId;\n    },\n\n    increaseQty() {\n      this.packageQuantity++;\n    },\n\n    decreaseQty() {\n      if (this.packageQuantity > 0) {\n        this.packageQuantity--;\n      }\n    },\n\n    addPackage() {\n        if (this.canAddPackage) {\n            // Aggiungi il pacchetto alla lista\n            this.addedPackages.push({\n            type: this.selectedPackageType,\n            quantity: this.packageQuantity\n            });\n\n            console.log(\"Imballo aggiunto:\", {\n            type: this.selectedPackageType,\n            quantity: this.packageQuantity\n            });\n\n            // Reset dei campi\n            this.selectedPackageType = 'NESSUNO';\n            this.packageQuantity = 0;\n            this.packageSelected = true;\n        }\n    },\n\n    removePackage(index) {\n        this.addedPackages.splice(index, 1);\n        console.log(\"Imballo rimosso, indice:\", index);\n    },\n\n    async saveAndContinue() {\n\n    this.localLoading = true;\n\n    try {\n        // Salva gli imballi al database\n        console.log(\"Salvataggio imballi:\", this.addedPackages);\n        if(this.addedPackages && this.addedPackages.length > 0)\n        {\n            const response = await axios.post(`/documents/${this.saleDocId}/packages`, {\n            packages: this.addedPackages\n            });\n\n            if (response.data.success) {\n            console.log(\"Imballi salvati con successo\");\n            }\n        }\n        // Navigazione al riepilogo\n        this.$router.push({\n            name: 'document-summary',\n            params: { saleDocId: this.saleDocId }\n        });\n    } catch (err) {\n        console.error(\"Eccezione nel salvataggio degli imballi:\", err);\n        this.error = \"Errore nella comunicazione con il server\";\n    } finally {\n        this.localLoading = false;\n    }\n    },\n\n    goToScan() {\n      this.$router.push({ name: 'scan-qr' });\n    }\n  },\n\n  watch: {\n    selectedPackageType(newValue) {\n      if (newValue) {\n        this.packageSelected = true;\n      }\n    }\n  }\n};\n</script>\n\n<style scoped>\n.items-list {\n  padding: 20px;\n  max-width: 500px;\n  margin: 0 auto;\n  position: relative;\n  min-height: 80vh;\n}\n\n.loading-overlay {\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: rgba(255, 255, 255, 0.9);\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  z-index: 10;\n}\n\n.loading-spinner {\n  border: 4px solid #f3f3f3;\n  border-top: 4px solid #3490dc;\n  border-radius: 50%;\n  width: 40px;\n  height: 40px;\n  animation: spin 1s linear infinite;\n  margin-bottom: 10px;\n}\n\n@keyframes spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n.loading-text {\n  font-size: 16px;\n  color: #333;\n}\n\n.error-message {\n  background-color: #f8d7da;\n  color: #721c24;\n  padding: 15px;\n  border-radius: 4px;\n  margin-bottom: 20px;\n  text-align: center;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 10px;\n}\n\n.error-icon {\n  font-size: 24px;\n  margin-bottom: 5px;\n}\n\n.retry-button {\n  background-color: #3490dc;\n  color: white;\n  border: none;\n  padding: 8px 16px;\n  border-radius: 4px;\n  cursor: pointer;\n  font-size: 14px;\n  margin-top: 10px;\n}\n\n.logo-header {\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n.logo-header img {\n  max-width: 120px;\n  height: auto;\n}\n\n.client-info {\n  background-color: #f5f5f5;\n  padding: 10px 15px;\n  margin-bottom: 10px;\n  border-radius: 4px;\n}\n\n.document-info {\n  margin-bottom: 20px;\n}\n\n.info-row {\n  display: flex;\n  justify-content: space-between;\n}\n\n.info-col {\n  flex: 1;\n  padding: 10px 15px;\n  background-color: #f5f5f5;\n  border-radius: 4px;\n}\n\n.info-col:first-child {\n  margin-right: 10px;\n}\n\n.info-label {\n  font-size: 12px;\n  font-weight: bold;\n  color: #666;\n  margin-bottom: 5px;\n}\n\n.info-value {\n  font-size: 16px;\n  font-weight: bold;\n}\n\n.section-title {\n  font-size: 16px;\n  font-weight: bold;\n  margin-bottom: 15px;\n}\n\n.imballi-section {\n  margin-bottom: 20px;\n}\n\n.select-container {\n  margin-bottom: 15px;\n}\n\n.form-control {\n  width: 100%;\n  padding: 10px;\n  border: 1px solid #ced4da;\n  border-radius: 4px;\n  font-size: 16px;\n  appearance: none;\n  background-image: url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='ht