___CSS_LOADER_EXPORT___.push([module.id, "\n.confirmation[data-v-37f464fb] {\n    padding: 20px;\n    max-width: 500px;\n    margin: 0 auto;\n    position: relative;\n    min-height: 80vh;\n}\n.loading-overlay[data-v-37f464fb] {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: rgba(255, 255, 255, 0.9);\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    z-index: 10;\n}\n.loading-spinner[data-v-37f464fb] {\n    border: 4px solid #f3f3f3;\n    border-top: 4px solid #3490dc;\n    border-radius: 50%;\n    width: 40px;\n    height: 40px;\n    animation: spin-37f464fb 1s linear infinite;\n    margin-bottom: 10px;\n}\n@keyframes spin-37f464fb {\n0% { transform: rotate(0deg);\n}\n100% { transform: rotate(360deg);\n}\n}\n.loading-text[data-v-37f464fb] {\n    font-size: 16px;\n    color: #333;\n}\n.error-message[data-v-37f464fb] {\n    background-color: #f8d7da;\n    color: #721c24;\n    padding: 15px;\n    border-radius: 4px;\n    margin-bottom: 20px;\n    text-align: center;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 10px;\n}\n.error-icon[data-v-37f464fb] {\n    font-size: 24px;\n    margin-bottom: 5px;\n}\n.retry-button[data-v-37f464fb] {\n    background-color: #3490dc;\n    color: white;\n    border: none;\n    padding: 8px 16px;\n    border-radius: 4px;\n    cursor: pointer;\n    font-size: 14px;\n    margin-top: 10px;\n}\n.logo-header[data-v-37f464fb] {\n    text-align: center;\n    margin-bottom: 20px;\n}\n.logo-header img[data-v-37f464fb] {\n    max-width: 120px;\n    height: auto;\n}\n.client-info[data-v-37f464fb] {\n    background-color: #f5f5f5;\n    padding: 10px 15px;\n    margin-bottom: 10px;\n    border-radius: 4px;\n}\n.document-info[data-v-37f464fb] {\n    margin-bottom: 20px;\n}\n.info-row[data-v-37f464fb] {\n    display: flex;\n    justify-content: space-between;\n}\n.info-col[data-v-37f464fb] {\n    flex: 1;\n    padding: 10px 15px;\n    background-color: #f5f5f5;\n    border-radius: 4px;\n}\n.info-col[data-v-37f464fb]:first-child {\n    margin-right: 10px;\n}\n.info-label[data-v-37f464fb] {\n    font-size: 12px;\n    font-weight: bold;\n    color: #666;\n    margin-bottom: 5px;\n}\n.info-value[data-v-37f464fb] {\n    font-size: 16px;\n    font-weight: bold;\n}\n.summary-title[data-v-37f464fb] {\n    margin: 20px 0 15px;\n    font-size: 16px;\n    font-weight: bold;\n    text-align: center;\n}\n.summary-section[data-v-37f464fb] {\n    margin-bottom: 20px;\n}\n.section-title[data-v-37f464fb] {\n    font-size: 14px;\n    font-weight: bold;\n    margin-bottom: 10px;\n    padding: 8px 12px;\n    border-radius: 4px;\n    text-align: center;\n}\n.section-title.consegnato[data-v-37f464fb] {\n    background-color: #d4edda;\n    color: #155724;\n    border: 1px solid #c3e6cb;\n}\n.section-title.reso[data-v-37f464fb] {\n    background-color: #fff3cd;\n    color: #856404;\n    border: 1px solid #ffeaa7;\n}\n.summary-list[data-v-37f464fb] {\n    background-color: #f5f5f5;\n    padding: 15px;\n    border-radius: 4px;\n}\n.summary-list ul[data-v-37f464fb] {\n    list-style: none;\n    padding: 0;\n    margin: 0;\n}\n.summary-list li[data-v-37f464fb] {\n    padding: 8px 0;\n    border-bottom: 1px solid #ddd;\n}\n.summary-list li[data-v-37f464fb]:last-child {\n    border-bottom: none;\n}\n.detail-item[data-v-37f464fb] {\n    display: flex;\n    justify-content: space-between;\n    align-items: flex-start;\n}\n.detail-info[data-v-37f464fb] {\n    flex: 1;\n    margin-right: 10px;\n}\n.detail-code[data-v-37f464fb] {\n    font-weight: bold;\n    margin-bottom: 2px;\n}\n.detail-description[data-v-37f464fb] {\n    font-size: 14px;\n    color: #666;\n}\n.detail-quantity[data-v-37f464fb] {\n    font-weight: bold;\n    min-width: 60px;\n    text-align: right;\n}\n.action-button[data-v-37f464fb] {\n    text-align: center;\n    margin-top: 30px;\n}\n.btn-confirm[data-v-37f464fb] {\n    background-color: #3490dc;\n    color: white;\n    border: none;\n    padding: 12px 40px;\n    border-radius: 25px;\n    font-size: 16px;\n    font-weight: bold;\n    cursor: pointer;\n    transition: background-color 0.3s;\n}\n.btn-confirm[data-v-37f464fb]:hover:not(:disabled) {\n    background-color: #2779bd;\n}\n.btn-confirm[data-v-37f464fb]:disabled {\n    background-color: #a0c5e8;\n    cursor: not-allowed;\n}\n.action-buttons[data-v-37f464fb] {\n  display: flex;\n  justify-content: space-between;\n  margin-top: 30px;\n}\n.btn-cancel[data-v-37f464fb] {\n  background-color: #dc3545;\n  color: white;\n  border: none;\n  padding: 12px 30px;\n  border-radius: 25px;\n  font-size: 16px;\n  font-weight: bold;\n  cursor: pointer;\n  transition: background-color 0.3s;\n}\n.btn-cancel[data-v-37f464fb]:hover {\n  background-color: #c82333;\n}\n.loading-text[data-v-37f464fb], .empty-message[data-v-37f464fb] {\n  text-align: center;\n  padding: 20px;\n  color: #6c757d;\n}\n  ", "",{"version":3,"sources":["webpack://./resources/js/components/Ddt/Confirmation.vue"],"names":[],"mappings":";AA0UA;IACA,aAAA;IACA,gBAAA;IACA,cAAA;IACA,kBAAA;IACA,gBAAA;AACA;AAEA;IACA,kBAAA;IACA,MAAA;IACA,OAAA;IACA,QAAA;IACA,SAAA;IACA,0CAAA;IACA,aAAA;IACA,sBAAA;IACA,uBAAA;IACA,mBAAA;IACA,WAAA;AACA;AAEA;IACA,yBAAA;IACA,6BAAA;IACA,kBAAA;IACA,WAAA;IACA,YAAA;IACA,2CAAA;IACA,mBAAA;AACA;AAEA;AACA,KAAA,uBAAA;AAAA;AACA,OAAA,yBAAA;AAAA;AACA;AAEA;IACA,eAAA;IACA,WAAA;AACA;AAEA;IACA,yBAAA;IACA,cAAA;IACA,aAAA;IACA,kBAAA;IACA,mBAAA;IACA,kBAAA;IACA,aAAA;IACA,sBAAA;IACA,mBAAA;IACA,SAAA;AACA;AAEA;IACA,eAAA;IACA,kBAAA;AACA;AAEA;IACA,yBAAA;IACA,YAAA;IACA,YAAA;IACA,iBAAA;IACA,kBAAA;IACA,eAAA;IACA,eAAA;IACA,gBAAA;AACA;AAEA;IACA,kBAAA;IACA,mBAAA;AACA;AAEA;IACA,gBAAA;IACA,YAAA;AACA;AAEA;IACA,yBAAA;IACA,kBAAA;IACA,mBAAA;IACA,kBAAA;AACA;AAEA;IACA,mBAAA;AACA;AAEA;IACA,aAAA;IACA,8BAAA;AACA;AAEA;IACA,OAAA;IACA,kBAAA;IACA,yBAAA;IACA,kBAAA;AACA;AAEA;IACA,kBAAA;AACA;AAEA;IACA,eAAA;IACA,iBAAA;IACA,WAAA;IACA,kBAAA;AACA;AAEA;IACA,eAAA;IACA,iBAAA;AACA;AAEA;IACA,mBAAA;IACA,eAAA;IACA,iBAAA;IACA,kBAAA;AACA;AAEA;IACA,mBAAA;AACA;AAEA;IACA,eAAA;IACA,iBAAA;IACA,mBAAA;IACA,iBAAA;IACA,kBAAA;IACA,kBAAA;AACA;AAEA;IACA,yBAAA;IACA,cAAA;IACA,yBAAA;AACA;AAEA;IACA,yBAAA;IACA,cAAA;IACA,yBAAA;AACA;AAEA;IACA,yBAAA;IACA,aAAA;IACA,kBAAA;AACA;AAEA;IACA,gBAAA;IACA,UAAA;IACA,SAAA;AACA;AAEA;IACA,cAAA;IACA,6BAAA;AACA;AAEA;IACA,mBAAA;AACA;AAEA;IACA,aAAA;IACA,8BAAA;IACA,uBAAA;AACA;AAEA;IACA,OAAA;IACA,kBAAA;AACA;AAEA;IACA,iBAAA;IACA,kBAAA;AACA;AAEA;IACA,eAAA;IACA,WAAA;AACA;AAEA;IACA,iBAAA;IACA,eAAA;IACA,iBAAA;AACA;AAEA;IACA,kBAAA;IACA,gBAAA;AACA;AAEA;IACA,yBAAA;IACA,YAAA;IACA,YAAA;IACA,kBAAA;IACA,mBAAA;IACA,eAAA;IACA,iBAAA;IACA,eAAA;IACA,iCAAA;AACA;AAEA;IACA,yBAAA;AACA;AAEA;IACA,yBAAA;IACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,gBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;EACA,YAAA;EACA,kBAAA;EACA,mBAAA;EACA,eAAA;EACA,iBAAA;EACA,eAAA;EACA,iCAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,kBAAA;EACA,aAAA;EACA,cAAA;AACA","sourcesContent":["<template>\n    <div class=\"confirmation\">\n      <!-- Mostra loader durante il caricamento -->\n      <div v-if=\"loading\" class=\"loading-overlay\">\n        <div class=\"loading-spinner\"></div>\n        <div class=\"loading-text\">Caricamento in corso...</div>\n      </div>\n\n      <!-- Mostra errore se presente -->\n      <div v-else-if=\"error\" class=\"error-message\">\n        <div class=\"error-icon\">⚠️</div>\n        <div>{{ error }}</div>\n        <button @click=\"loadDocumentWithDetails\" class=\"retry-button\">Riprova</button>\n      </div>\n\n      <template v-else-if=\"document\">\n      <!-- Intestazione rimane uguale -->\n      <div class=\"logo-header\">\n        <img src=\"/img/logo.png\" alt=\"B&C Prodotti Chimici\" />\n      </div>\n\n      <div class=\"client-info\">\n        <div class=\"info-label\">CLIENTE:</div>\n        <div class=\"info-value\">{{ customerName }}</div>\n      </div>\n\n      <div class=\"document-info\">\n        <div class=\"info-row\">\n          <div class=\"info-col\">\n            <div class=\"info-label\">DDT NR.</div>\n            <div class=\"info-value\">{{ docNumber }}</div>\n          </div>\n          <div class=\"info-col\">\n            <div class=\"info-label\">DATA</div>\n            <div class=\"info-value\">{{ formattedDate }}</div>\n          </div>\n        </div>\n      </div>\n\n        <div class=\"summary-title\">\n          RIEPILOGO\n        </div>\n\n        <!-- Sezione Consegnato -->\n        <div class=\"summary-section\">\n          <div class=\"section-title consegnato\">CONSEGNATO</div>\n          <div class=\"summary-list\">\n            <div v-if=\"loadingRows\" class=\"loading-text\">Caricamento righe...</div>\n            <div v-else-if=\"deliveredRows.length === 0\" class=\"empty-message\">Nessun prodotto consegnato</div>\n            <ul v-else>\n              <li v-for=\"(row, index) in deliveredRows\" :key=\"'delivered-' + index\">\n                <div class=\"detail-item\">\n                  <div class=\"detail-info\">\n                    <div class=\"detail-code\">{{ row.Item }}</div>\n                    <div class=\"detail-description\">{{ getItemDescription(row) }}</div>\n                  </div>\n                  <div class=\"detail-quantity\">\n                    {{ formatQuantity(row.Qty) }} {{ row.UoM || '' }}\n                  </div>\n                </div>\n              </li>\n            </ul>\n          </div>\n        </div>\n\n        <!-- Sezione Reso -->\n        <div class=\"summary-section\">\n          <div class=\"section-title reso\">RESO</div>\n          <div class=\"summary-list\">\n            <div v-if=\"loadingRows\" class=\"loading-text\">Caricamento righe...</div>\n            <div v-else-if=\"returnedRows.length === 0\" class=\"empty-message\">Nessun imballo reso</div>\n            <ul v-else>\n              <li v-for=\"(row, index) in returnedRows\" :key=\"'returned-' + index\">\n                <div class=\"detail-item\">\n                  <div class=\"detail-info\">\n                    <div class=\"detail-code\">{{ row.Item }}</div>\n                    <div class=\"detail-description\">{{ getItemDescription(row) }}</div>\n                  </div>\n                  <div class=\"detail-quantity\">\n                    {{ formatQuantity(row.Qty) }} {{ row.UoM || '' }}\n                  </div>\n                </div>\n              </li>\n            </ul>\n          </div>\n        </div>\n\n      <div class=\"action-buttons\">\n        <!-- Pulsante Annulla -->\n        <button @click=\"cancelAndReturn\" class=\"btn-cancel\">\n          ANNULLA\n        </button>\n        <!-- Pulsante Conferma -->\n        <button @click=\"confirmDocument\" class=\"btn-confirm\" :disabled=\"confirmLoading\">\n          {{ confirmLoading ? 'CONFERMA IN CORSO...' : 'CONFERMA' }}\n        </button>\n    </div>\n      </template>\n\n      <!-- Fallback se non ci sono dati -->\n      <div v-else class=\"error-message\">\n        <div>Nessun dato disponibile per il riepilogo.</div>\n        <button @click=\"goToScan\" class=\"retry-button\">Torna alla scansione</button>\n      </div>\n    </div>\n  </template>\n\n  <script>\n  import { mapState, mapGetters, mapActions } from 'vuex';\n\n  export default {\n    data() {\n      return {\n        saleDocId: null,\n        confirmLoading: false,\n        localLoading: false,\n        loadingRows: false,\n        rows: [],\n        error: null\n      };\n    },\n\n    computed: {\n      ...mapState('ddt', ['document', 'details', 'customer']),\n      ...mapGetters('ddt', ['isLoading', 'hasError', 'errorMessage']),\n\n      loading() {\n        return this.isLoading || this.localLoading;\n      },\n\n      error() {\n        return this.errorMessage;\n      },\n\n      customerName() {\n        return this.customer?.CompanyName || '';\n      },\n\n      docNumber() {\n        return this.document?.DocNo || '';\n      },\n\n      formattedDate() {\n        if (!this.document?.DocumentDate) return '';\n\n        try {\n          const date = new Date(this.document.DocumentDate);\n          const day = date.getDate().toString().padStart(2, '0');\n          const month = (date.getMonth() + 1).toString().padStart(2, '0');\n          const year = date.getFullYear();\n\n          return `${day}/${month}/${year}`;\n        } catch (e) {\n          console.error(\"Errore nella formattazione della data:\", e);\n          return this.document.DocumentDate || '';\n        }\n      },\n\n      // Righe consegnate (Line > 0)\n      deliveredRows() {\n        return this.rows.filter(row => row.Line > 0);\n      },\n\n      // Righe rese/imballi (Line = 0)\n      returnedRows() {\n        return this.rows.filter(row => row.Line == 0);\n      }\n    },\n\n    created() {\n      console.log(\"Confirmation creato\");\n      this.initializeComponent();\n    },\n\n    watch: {\n      // Osserva i cambiamenti nei parametri della rotta\n      '$route.params': {\n        handler(newParams) {\n          console.log(\"Parametri rotta cambiati:\", newParams);\n          this.initializeComponent();\n        },\n        deep: true,\n        immediate: true\n      }\n    },\n\n    methods: {\n        ...mapActions('ddt', {\n            fetchDocumentSummaryAction: 'fetchDocumentSummary',\n            confirmDocumentAction: 'confirmDocument'\n        }),\n\n      initializeComponent() {\n        this.saleDocId = this.$route.params.saleDocId;\n\n        console.log(\"Inizializzazione con saleDocId:\", this.saleDocId);\n\n        if (this.saleDocId) {\n          this.loadDocumentWithDetails();\n          this.loadRegisteredRows();\n        } else {\n          console.error(\"SaleDocId mancante\");\n        }\n      },\n\n      async loadDocumentWithDetails() {\n        console.log(\"Caricamento riepilogo documento:\", this.saleDocId);\n\n        if (!this.saleDocId) {\n          console.error(\"SaleDocId non impostato\");\n          return;\n        }\n\n        this.localLoading = true;\n\n        try {\n          const response = await this.fetchDocumentSummary({\n            saleDocId: this.saleDocId\n          });\n\n          console.log(\"Risposta caricamento:\", response);\n\n          if (response.success) {\n            console.log(\"Caricamento riuscito - Document:\", this.document);\n            console.log(\"Caricamento riuscito - Details:\", this.details);\n          } else {\n            console.error(\"Errore caricamento:\", response);\n          }\n        } catch (err) {\n          console.error(\"Eccezione nel caricamento del riepilogo:\", err);\n        } finally {\n          this.localLoading = false;\n        }\n      },\n      async loadRegisteredRows() {\n      this.loadingRows = true;\n\n      try {\n        // Carica le righe registrate nella tabella DEB_BA4_RigheAutisti\n        const response = await axios.get(`/documents/${this.saleDocId}/registered-rows`);\n\n        if (response.data.success) {\n          console.log(\"Righe registrate:\", response.data.rows);\n          this.rows = response.data.rows;\n        } else {\n          console.error(\"Errore nel caricamento delle righe:\", response.data.message);\n          this.error = \"Errore nel caricamento delle righe\";\n        }\n      } catch (err) {\n        console.error(\"Eccezione nel caricamento delle righe:\", err);\n        this.error = \"Errore nella comunicazione con il server\";\n      } finally {\n        this.loadingRows = false;\n      }\n    },\n\n    getItemDescription(row) {\n      // Se è una riga di dettaglio (Line > 0), usa la descrizione dalla tabella MA_SaleDocDetail\n      if (row.Line > 0) {\n        // Cerca nel dettaglio del documento\n        const detail = this.details.find(d => d.Line === row.Line);\n        return detail ? detail.Description : row.Item;\n      }\n\n      // Se è un imballo (Line = 0), cerca la descrizione nei packages\n      return row.Description || row.Item;\n    },\n\n    async confirmDocument() {\n    console.log(\"Conferma documento:\", this.saleDocId);\n\n    this.confirmLoading = true;\n\n    try {\n        const response = await this.confirmDocumentAction({\n        saleDocId: this.saleDocId\n        });\n\n        console.log(\"Risposta conferma:\", response);\n\n        if (response.success) {\n        // Mostra il messaggio di successo\n        this.$router.push({ name: 'confirmation-success' });\n        } else {\n        console.error(\"Errore conferma:\", response);\n        this.error = response.message || \"Errore durante la conferma\";\n        }\n    } catch (err) {\n        console.error(\"Eccezione nella conferma del documento:\", err);\n        this.error = \"Si è verificato un errore durante la conferma\";\n    } finally {\n        this.confirmLoading = false;\n    }\n    },\n\n    async cancelAndReturn() {\n    console.log(\"Cancellazione righe e ritorno alla scansione\");\n\n    this.localLoading = true;\n\n    try {\n        // Cancella tutte le righe registrate per questo documento\n        const response = await axios.delete(`/documents/${this.saleDocId}/registered-rows`);\n\n        console.log(\"Risposta cancellazione:\", response.data);\n\n        // Torna alla pagina di scansione QR code\n        this.$router.push({ name: 'scan-qr' });\n    } catch (err) {\n        console.error(\"Errore nella cancellazione delle righe:\", err);\n        this.error = \"Errore nella cancellazione delle righe\";\n        this.localLoading = false;\n    }\n    },\n\n      formatQuantity(qty) {\n        if (qty === null || qty === undefined) return '0';\n\n        // Formatta il numero con massimo 3 decimali\n        return parseFloat(qty).toFixed(3).replace(/\\.?0+$/, '');\n      },\n\n      goToScan() {\n        this.$router.push({ name: 'scan-qr' });\n      }\n    }\n  };\n  </script>\n\n  <style scoped>\n  .confirmation {\n    padding: 20px;\n    max-width: 500px;\n    margin: 0 auto;\n    position: relative;\n    min-height: 80vh;\n  }\n\n  .loading-overlay {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: rgba(255, 255, 255, 0.9);\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    z-index: 10;\n  }\n\n  .loading-spinner {\n    border: 4px solid #f3f3f3;\n    border-top: 4px solid #3490dc;\n    border-radius: 50%;\n    width: 40px;\n    height: 40px;\n    animation: spin 1s linear infinite;\n    margin-bottom: 10px;\n  }\n\n  @keyframes spin {\n    0% { transform: rotate(0deg); }\n    100% { transform: rotate(360deg); }\n  }\n\n  .loading-text {\n    font-size: 16px;\n    color: #333;\n  }\n\n  .error-message {\n    background-color: #f8d7da;\n    color: #721c24;\n    padding: 15px;\n    border-radius: 4px;\n    margin-bottom: 20px;\n    text-align: center;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 10px;\n  }\n\n  .error-icon {\n    font-size: 24px;\n    margin-bottom: 5px;\n  }\n\n  .retry-button {\n    background-color: #3490dc;\n    color: white;\n    border: none;\n    padding: 8px 16px;\n    border-radius: 4px;\n    cursor: pointer;\n    font-size: 14px;\n    margin-top: 10px;\n  }\n\n  .logo-header {\n    text-align: center;\n    margin-bottom: 20px;\n  }\n\n  .logo-header img {\n    max-width: 120px;\n    height: auto;\n  }\n\n  .client-info {\n    background-color: #f5f5f5;\n    padding: 10px 15px;\n    margin-bottom: 10px;\n    border-radius: 4px;\n  }\n\n  .document-info {\n    margin-bottom: 20px;\n  }\n\n  .info-row {\n    display: flex;\n    justify-content: space-between;\n  }\n\n  .info-col {\n    flex: 1;\n    padding: 10px 15px;\n    background-color: #f5f5f5;\n    border-radius: 4px;\n  }\n\n  .info-col:first-child {\n    margin-right: 10px;\n  }\n\n  .info-label {\n    font-size: 12px;\n    font-weight: bold;\n    color: #666;\n    margin-bottom: 5px;\n  }\n\n  .info-value {\n    font-size: 16px;\n    font-weight: bold;\n  }\n\n  .summary-title {\n    margin: 20px 0 15px;\n    font-size: 16px;\n    font-weight: bold;\n    text-align: center;\n  }\n\n  .summary-section {\n    margin-bottom: 20px;\n  }\n\n  .section-title {\n    font-size: 14px;\n    font-weight: bold;\n    margin-bottom: 10px;\n    padding: 8px 12px;\n    border-radius: 4px;\n    text-align: center;\n  }\n\n  .section-title.consegnato {\n    background-color: #d4edda;\n    color: #155724;\n    border: 1px solid #c3e6cb;\n  }\n\n  .section-title.reso {\n    background-color: #fff3cd;\n    color: #856404;\n    border: 1px solid #ffeaa7;\n  }\n\n  .summary-list {\n    background-color: #f5f5f5;\n    padding: 15px;\n    border-radius: 4px;\n  }\n\n  .summary-list ul {\n    list-style: none;\n    padding: 0;\n    margin: 0;\n  }\n\n  .summary-list li {\n    padding: 8px 0;\n    border-bottom: 1px solid #ddd;\n  }\n\n  .summary-list li:last-child {\n    border-bottom: none;\n  }\n\n  .detail-item {\n    display: flex;\n    justify-content: space-between;\n    align-items: flex-start;\n  }\n\n  .detail-info {\n    flex: 1;\n    margin-right: 10px;\n  }\n\n  .detail-code {\n    font-weight: bold;\n    margin-bottom: 2px;\n  }\n\n  .detail-description {\n    font-size: 14px;\n    color: #666;\n  }\n\n  .detail-quantity {\n    font-weight: bold;\n    min-width: 60px;\n    text-align: right;\n  }\n\n  .action-button {\n    text-align: center;\n    margin-top: 30px;\n  }\n\n  .btn-confirm {\n    background-color: #3490dc;\n    color: white;\n    border: none;\n    padding: 12px 40px;\n    border-radius: 25px;\n    font-size: 16px;\n    font-weight: bold;\n    cursor: pointer;\n    transition: background-color 0.3s;\n  }\n\n  .btn-confirm:hover:not(:disabled) {\n    background-color: #2779bd;\n  }\n\n  .btn-confirm:disabled {\n    background-color: #a0c5e8;\n    cursor: not-allowed;\n  }\n\n  .action-buttons {\n  display: flex;\n  justify-content: space-between;\n  margin-top: 30px;\n}\n\n.btn-cancel {\n  background-color: #dc3545;\n  colo